.birkenhead {
  position: relative;
  width: 30%;
}

.birkenhead.active {
  width: auto;
}

.text-overlay {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100%;
  width: 100%;
  opacity: 0;
  transition: .5s ease;
  background-color: #000;
  font-family: 'Rubik', sans-serif;
}

.text-overlay:hover {
  opacity: 0.8;
}

.birkenhead-pigeon {
  width: 100%;
}

.text {
  color: white;
  opacity: 1;
  font-size: 0.5rem;
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  text-align: center;
}

@media screen and (max-width: 800px) {
  .birkenhead {
    position: relative;
    width: 100%;
  }
  
  .text-overlay {
    position: absolute;
